.auth-container{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.login-form-1,
.login-form-2{
  border-radius: 5px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
  background: #3174ad;
  padding: 2%;
  width: 30%;
}
.login-form-2 h3,
.login-form-1 h3{
  text-align: center;
  color: #fff;
}
.auth-container form{
  padding: 10%;
}
.btnSubmit
{
  width: 50%;
  border-radius: 1rem;
  padding: 1.5%;
  border: none;
  cursor: pointer;
}
.login-form-2 .btnSubmit,
.login-form-1 .btnSubmit{
  background-color: #fff;
  color: #3174ad;
}
.login-form-2 .ForgetPwd,
.login-form-1 .ForgetPwd{
  color: #fff;
  font-weight: 600;
  text-decoration: none;
}

.link {
  color: #fff;
  font-weight: 600;
  text-decoration: none;
}
a:hover {
  color: rgb(219, 219, 219);
}